    font-family: "Roboto500";
    font-style: normal;
    src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/Roboto-500.woff") format("woff")
}

@font-face {
  font-family: 'transmission';
  src:  url('../fonts/transmission.eot?fnz9q4');
  src:  url('../fonts/transmission.eot?fnz9q4#iefix') format('embedded-opentype'),
    url('../fonts/transmission.ttf?fnz9q4') format('truetype'),
    url('../fonts/transmission.woff?fnz9q4') format('woff'),
    url('../fonts/transmission.svg?fnz9q4#transmission') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'transmission' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

